@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans Bold";
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: normal;
  line-height: 24px;
  color: #444444;
  background: white no-repeat;
}
.main {
  position: relative;
  z-index: 1;
}
.sprite {
  display: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.hide {
  display: none;
}
.overlay {
  display: none;
  width: 100vw;
  height: 100vh;
  background-color: white;
  opacity: 0.6;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 12;
}
.overlay--on {
  display: block;
}
.separator {
  width: 100%;
  height: 6px;
  position: relative;
}
.separator::before {
  content: "";
  width: 100%;
  height: 6px;
  position: absolute;
  background: url("../img/bg-zigzag-line.svg") repeat-x;
  background-size: contain;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}
.menu-closed {
  display: none;
}
@media (min-width: 768px) {
  .separator::before {
    height: 16px;
  }
}
@media (min-width: 1200px) {
  .separator::before {
    height: 25px;
  }
}
.header {
  margin: 0;
  background-color: white;
  font: inherit;
  font-size: 17px;
  font-weight: bold;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .header {
    padding: 0;
  }
