    height: 500px;
    position: absolute;
    top: 88px;
    left: 0;
    background-color: #f1f1f1;
    border-top: 1px solid #ececec;
  }
  .header--index::after {
    background-color: #f9f9f9;
    content: "";
    width: 100%;
    height: 500px;
    position: absolute;
    top: 88px;
    left: 0;
    border-top: 1px solid #ececec;
  }
}
